@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap");
/** Media Queries **/
*, ::before, ::after {
  box-sizing: border-box;
}

body {
  position: relative;
  padding: 3rem 1rem 0 1rem;
  font-family: "Nunito", sans-serif;
  transition: 0.5s;
}
@media (min-width: 768px) {
  body {
    padding-left: calc(68px + 2rem);
  }
}

a {
  text-decoration: none;
}

.header {
  width: 100%;
  height: 3rem;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  background-color: #FFFFFF;
  z-index: 100;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .header {
    height: 4rem;
    padding: 0 2rem 0 calc(68px + 2rem);
  }
}

.header__toogle {
  color: #4723D9;
  font-size: 1.5rem;
  cursor: pointer;
}

.header__img {
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden;
}
@media (min-width: 768px) {
  .header__img {
    width: 40px;
    height: 40px;
  }
}

.header__img img {
  width: 40px;
}
@media (min-width: 768px) {
  .header__img img {
    width: 45px;
  }
}

/*=========NAV=========*/
.l-navbar {
  position: fixed;
  top: 0;
  left: -30%;
  width: 68px;
  height: 100vh;
  background-color: #4723D9;
  padding: 0.5rem 1rem 0 0;
  transition: 0.5s;
  z-index: 100;
}
@media (min-width: 768px) {
  .l-navbar {
    left: 0;
    padding: 1rem 1rem 0 0;
  }
}

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.nav__logo, .nav__link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 1rem;
  padding: 0.5rem 0 0.5rem 1.5rem;
}

.nav__logo {
  margin-bottom: 2rem;
}

.nav__logo-icon {
  font-size: 1.25rem;
  color: #FFFFFF;
}

.nav__logo-name {
  color: #FFFFFF;
  font-weight: 700;
}

.nav__link {
  position: relative;
  color: #AFA5D9;
  margin-bottom: 1.5rem;
  transition: 0.3s;
}

.nav__link:hover {
  color: #FFFFFF;
}

.nav__icon {
  font-size: 1.25rem;
}

/* Show Navbar*/
.show {
  left: 0;
}
@media (min-width: 768px) {
  .show {
    width: 224px;
  }
}

.body-pd {
  padding-left: calc(68px + 1rem);
}
@media (min-width: 768px) {
  .body-pd {
    padding-left: 256px;
  }
}

.active {
  color: #FFFFFF;
}

.active::before {
  content: "";
  position: absolute;
  left: 0;
  width: 2px;
  height: 32px;
  background-color: #FFFFFF;
}

.main {
  background-color: chartreuse;
  height: 500rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}